@import url('https://fonts.googleapis.com/css2?family=Maven+Pro:wght@400;500;600;700;800;900&display=swap');
$main: #CF8F24;
$secondary: #ECE0CC;
$grey:rgb(61, 61, 61);
$color1: #1999A8;
$color2: #1CAF95;
$gradient: linear-gradient(to right,$color1,$color2);
$gradient2: linear-gradient(to left,$color1,$color2);
$background:#ffffff;
$shadow: #e8e8e8;
$shadow2: #e8e8e8d7;

@font-face {
    font-family: "Kraftan";
    src: local("Kraftan"),
      url('../styles/fonts/kraftan.otf') format("truetype");
 }